.radio {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-align: center;
  align-items: center;
  width: 100%;
}
.radio__option {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  height: 50px;
  font-weight: 600;
  font-size: 15px;
  color: #B9BEC9;
  border: 3px solid #B9BEC9;
  border-radius: 2px;
  cursor: pointer;
}
.radio__option--active {
  color: #3751FF;
  border-color: #3751FF;
}
.radio__option:last-child {
  margin-bottom: 0 !important;
}



