.textarea {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  height: 30vh;
  max-height: 250px;
  width: 100%;
  padding: 15px;
  background-color: white;
  font-size: 14px;
  font-weight: 400;
  border: 3px solid #B9BEC9;
  border-radius: 2px;
  opacity: 1;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  resize: none;
}
.textarea:focus {
  outline: 0;
  border: 3px solid #3751FF;
  border-radius: 2px;
}
.textarea::-webkit-input-placeholder {
  color: #B9BEC9;
}
.textarea:-ms-input-placeholder {
  color: #B9BEC9;
}
.textarea::-ms-input-placeholder {
  color: #B9BEC9;
}
.textarea::placeholder {
  color: #B9BEC9;
}



