.button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  height: 50px;
  width: 100%;
  font-size: 12px;
  font-weight: 400;
  color: white;
  text-transform: uppercase;
  background-color: #3751FF;
  border: 0px;
  border-radius: 2px;
  opacity: 1;
  cursor: pointer;
}
.button:focus {
  outline: 0;
}
.button:hover {
  opacity: 0.8;
}
.button--disabled {
  opacity: 0.5;
  cursor: default;
}
.button--disabled:hover {
  opacity: 0.5;
}
.button svg {
  width: 20px;
  margin-left: 10px;
}
.button svg * {
  color: white;
}



