.input {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  height: 60px;
  width: 100%;
  padding: 0 15px;
  background-color: white;
  font-size: 14px;
  font-weight: 400;
  border: 3px solid #B9BEC9;
  border-radius: 2px;
  opacity: 1;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.input:focus {
  outline: 0;
  border: 3px solid #3751FF;
  border-radius: 2px;
}
.input::-webkit-input-placeholder {
  color: #B9BEC9;
}
.input:-ms-input-placeholder {
  color: #B9BEC9;
}
.input::-ms-input-placeholder {
  color: #B9BEC9;
}
.input::placeholder {
  color: #B9BEC9;
}



