.header {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-align: center;
  align-items: center;
  width: calc(100% - 80px);
  height: 80px;
  padding: 0 40px;
  left: 0;
  top: 0;
}
.header:focus {
  outline: 0;
}
.header div {
  display: none;
}
.header div p {
  font-size: 14px;
  font-weight: 500;
}
.header div p:last-child {
  font-size: 10px;
  text-transform: uppercase;
  margin-top: 5px;
}
.header div p:last-child a {
  color: #3751FF;
  cursor: pointer;
}
.header div p:last-child a:hover {
  opacity: 0.8;
}
.header svg {
  display: -ms-flexbox;
  display: flex;
  cursor: pointer;
}
.header svg:hover * {
  color: #3751FF;
}
.header__logo {
  width: 140px;
}
.header__menu {
  display: -ms-flexbox;
  display: flex;
  cursor: pointer;
}
.header__menu--active * {
  color: #3751FF;
}
@media (min-width: 960px) {
  .header {
    width: calc(100% - 380px);
  }
  .header div {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
  }
  .header svg {
    display: none;
  }
}



