.question {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-align: center;
  align-items: center;
  width: 100%;
}
.question__previous {
  font-size: 12px;
  color: #3751FF;
  text-transform: uppercase;
  margin-top: 20px;
  cursor: pointer;
}
.question__previous:hover {
  opacity: 0.8;
}
@media (min-width: 960px) {
  .question .button,
  .question .radio,
  .question .textarea,
  .question .input {
    width: 500px;
  }
}



